.App {
  max-width: 600px;
  max-height:1000px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: lightblue; /* semi-transparent white */
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  background-size: cover;
  background-position: center;
  text-align: center;
  transition: background-image 0.5s ease-in-out;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color:linear-gradient(90deg, rgba(0,212,255,1) 0%, rgba(9,9,121,1) 47%,rgba(2,0,36,1)  100%);
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  max-height: 100vh + 30vh;
}
h1, .weather-display h2, .weather-display p  {
  text-shadow: 1px 1px 2px rgba(0,0,0,0.1);
  color:white;
  
}
.heading{
  margin: auto;
  padding: auto;
  

}
.weather-search {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.search-input {
  padding: 10px 15px;
  font-size: 1rem;
  border: 2px solid #007bff;
  border-radius: 25px;
  outline: none;
  width: 70%;
  transition: all 0.3s ease;
}

.search-input:focus {
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.search-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 1rem;
  cursor: pointer;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
}

.search-button:hover {
  background-color: #0056b3;
  transform: scale(1.1);
}
.weather-display {
  text-align: center;
  
}

.weather-icon img {
  width: 100px;
  height: 100px;
}

.weather-details {
  display: flex;
  justify-content: space-around;
  margin-top: 1rem;
}

.weather-details p {
  display: flex;
  align-items: center;
  font-size: 1.8rem;
}

.weather-details svg {
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.weather-description {
  margin-top: 1rem;
  font-style: italic;
  font-size: 1.5rem;
}
.forecast-wrapper {
  margin-top: 2rem;
}

.daily-forecast, .hourly-forecast {
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  padding: 20px;
  margin:20px;
  text-align: center;
}


.daily-forecast h2 {
  text-align: center;
  padding: 20px;
  margin: 20px;
}
.forecast-wrapper {
  margin-top: 2rem;
}
.forecast-container {
  display: flex;
  justify-content: space-between;
  overflow-x: auto;
}

.forecast-day {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 80px;
}

.forecast-day p {
  margin: 0.5rem 0;
}
.toggle-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle-button:hover {
  background-color: #0056b3;
}

@media (max-width: 600px) {
  .forecast-container {
    justify-content: start;
  }
  
  .forecast-day {
    margin-right: 1rem;
  }
}
  form {
    display: flex;
    margin-bottom: 20px;
  }
  
  input {
    flex-grow: 1;
    padding: 10px;
    font-size: 16px;
  }
  
  button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  @media (max-width: 650px) {
    .App {
      margin: 0;
      padding: 1rem;
      border-radius: 0;
    }
  
    form {
      flex-direction: column;
    }
  
    input[type="text"], button {
      width: 100%;
      border-radius: 4px;
    }
  
    button {
      margin-top: 0.5rem;
    }
    
  }
  @media (max-width: 600px) {
    .weather-search {
      flex-direction: column;
    }
  
    .search-input {
      width: 100%;
      margin-bottom: 10px;
    }
  
    .search-button {
      margin-left: 0;
    }
  }